.site-header {
 border-top: 5px solid $grey-color-dark;
 min-height: 56px;
 position: relative;
}

.site-title {
 font-size: 26px;
 font-weight: 300;
 letter-spacing: -1px;
 margin-bottom: 0;
 float: left;
 text-shadow: -11px 0 50px #fff;

 &, &:visited {
 color: $grey-color-dark;
 }
}

.home-red{
 color:red;
 left:4px;
}
.home-yellow{
 color:gold;
 left:8px;
}
.home-green{
 color:green;
 left:12px;
}
.home-black{
 color:#000;
}
.slogan{
 font-size:20px;
 margin-left:20px;
 font-style:italic;
}
.site-nav {
 float: right;
 line-height: 56px;

 .menu-icon {
 display: none;
 }

 .page-link {
 color: $text-color;
 line-height: $base-line-height;

 // Gaps between nav items, but not on the last one
 &:not(:last-child) {
 margin-right: 20px;
 }
 }

 @include media-query($on-palm) {
 position: absolute;
 top: 9px;
 right: $spacing-unit / 2;
 background-color: $background-color;
 border: 1px solid $grey-color-light;
 border-radius: 5px;
 text-align: right;

 .menu-icon {
 display: block;
 float: right;
 width: 36px;
 height: 26px;
 line-height: 0;
 padding-top: 10px;
 text-align: center;

 > svg {
 width: 18px;
 height: 15px;

 path {
 fill: $grey-color-dark;
 }
 }
 }

 .trigger {
 clear: both;
 display: none;
 }

 &:hover .trigger {
 display: block;
 padding-bottom: 5px;
 }

 .page-link {
 display: block;
 padding: 5px 10px;

 &:not(:last-child) {
 margin-right: 0;
 }
 margin-left: 20px;
 }
 }
}


/**
 * Site footer
 */

.footer-heading {
 font-size: 18px;
 margin-bottom: $spacing-unit / 2;
}



.footer-col-wrapper {
 font-size: 15px;
 color: $grey-color;
 margin-left: -$spacing-unit / 2;
 @extend %clearfix;
}

.footer-col {
 float: left;
 margin-bottom: $spacing-unit / 2;
 padding-left: $spacing-unit / 2;
}

.footer-col-1, .footer-col-2, .footer-col-3 {
 width: 31%;
}

@include media-query($on-laptop) {
 .footer-col-1,
 .footer-col-2 {
 width: -webkit-calc(50% - (#{$spacing-unit} / 2));
 width: calc(50% - (#{$spacing-unit} / 2));
 }

 .footer-col-3 {
 width: -webkit-calc(100% - (#{$spacing-unit} / 2));
 width: calc(100% - (#{$spacing-unit} / 2));
 }
}

@include media-query($on-palm) {
 .footer-col {
 float: none;
 width: -webkit-calc(100% - (#{$spacing-unit} / 2));
 width: calc(100% - (#{$spacing-unit} / 2));
 }
}



/**
 * Page content
 */
.page-content {
 padding: 0;
}

.post-list {
 margin-left: 0;
 list-style: none;

 > li {
 margin-bottom: $spacing-unit;
 }
}

.post-meta {
 font-size: $small-font-size;
 color: $grey-color;
}

.post-link {
 display: block;
 font-size: 24px;
}
/**
 * Posts
 */
.post-header {
 margin-bottom: $spacing-unit;
}

.post-title {
 font-size: 30px;
 letter-spacing: -1px;
 line-height: 1;

 @include media-query($on-laptop) {
 font-size: 36px;
 }
}

.post-content {
 margin-bottom: $spacing-unit;

 h2 {
 font-size: 26px;

 @include media-query($on-laptop) {
 font-size: 28px;
 }
 }

 h3 {
 font-size: 24px;

 @include media-query($on-laptop) {
 font-size: 22px;
 }
 }

 h4 {
 font-size: 20px;

 @include media-query($on-laptop) {
 font-size: 18px;
 }
 }
}
/**
 * Contact
 */
.align-up{
 position:relative;
 top:-10px;
}
.align-down{
