/* Lightbox css */
.row > .column {
 padding: 0 8px;
}

.row:after {
 content: "";
 display: table;
 clear: both;
}

/* Create four equal columns that floats next to eachother */
.column {
 float: left;
 width: 25%;
}

/* The Modal (background) */
.modal {
 display: none;
 position: fixed;
 z-index: 1;
 padding-top: 100px;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 overflow: auto;
 background-color:black;

 a, a:hover{
 color:#2a7ae2;
 }

 .order_info{
 color:#fff;
 position: absolute;
 top: 20px;
 left:25px;
 font-style:italic;
 font-weight: lighter;
 letter-spacing:-1px;
 width:85%;
 }
 .more_products{
 color:#fff;
 position: absolute;
 top: 40px;
 left:25px;
 font-style:italic;
 font-weight: lighter;
 letter-spacing:-1px;
 width:85%;

 
 }

 @media screen and (max-width: 820px){
 .more_products{
 top:65px;
 }
 }
 @media screen and (max-width: 600px){
 .more_products{
 top:800px;

 }
 }
 @media screen and (max-width: 550px){
 .more_products{
 top:720px;
 }
 }

 @media screen and (max-width: 500px){
 .more_products{
 top:665px;
 }
 }

 @media screen and (max-width: 450px){
 .more_products{
 top:605px;
 }
 }

 @media screen and (max-width: 400px){
 .more_products{
 top:550px;
 }
 }

 @media screen and (max-width: 350px){
 .more_products{
 top:495px;
 }
 }

 @media screen and (max-width: 300px){
 .more_products{
 top:435px;
 }
 }
}

/* Modal Content */
.modal-content {
 position: relative;
 background-color: #fefefe;
 margin: auto;
 padding: 0;
 width: 90%;
 max-width: 600px;
 text-align:center;
}

/* The Close Button */
.close {
 color: white;
 position: absolute;
 top: 10px;
 right: 25px;
 font-size: 35px;
 font-weight: bold;
}

.close:hover,
.close:focus {
 color: #999;
 text-decoration: none;
 cursor: pointer;
}

/* Hide the slides by default */
.mySlides {
 display: none;
}

/* Next & previous buttons */
.prev,
.next {
 cursor: pointer;
 position: absolute;
 color:black;
 top: 50%;
 width: auto;
 padding: 16px;
 margin-top: -50px;
 font-weight: bold;
 font-size: 20px;
 border-radius: 0 3px 3px 0;
 user-select: none;
 -webkit-user-select: none;
}

/* Position the "next button" to the right */
.next {
 right: 0;
 border-radius: 3px 0 0 3px;
}
.prev{
 left:0;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
 background-color: rgba(0, 0, 0, 0.8);
 color:white;
 text-decoration:none;
}

/* Number text (1/3 etc) */
.numbertext {
